<template>
    <el-container style="height: 500px; border: 1px solid #eee">
        <el-header class="el-icon-s-home" style=" font-size: 40px; background-color:#409EFF; color:azure">
            教考系统</el-header>
        <el-footer>
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                <el-menu-item index="1"><router-link to="/leader" tag="span">账号信息</router-link></el-menu-item>
                <el-menu-item index="2"><router-link to="/studentManagement"
                        tag="span">学生信息管理管理</router-link></el-menu-item>
                <el-menu-item index="3"><router-link to="/teacherManagement"
                        tag="span">教师信息管理</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/courseManagement"
                        tag="span">课程信息管理</router-link></el-menu-item>
                <el-menu-item index="5"><router-link to="/studentLogin" tag="span">退出登录</router-link></el-menu-item>
            </el-menu>
        </el-footer>

        <el-main>

            <el-form :model="inputData" :inline="true" label-width="50px" style="width: 500px;"
                class="demo-form-inline">
                <el-form-item prop="key">
                    <el-input v-model="inputData" placeholder="请输入关键字以搜索"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="likeGet">查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="dialogFormVisible2 = true">添加</el-button>
                </el-form-item>
            </el-form>



            <el-table :data="courseData" style="width: 100%">
                <el-table-column prop="courseName" label="课程名称" width="200">
                </el-table-column>
                <el-table-column prop="courseNum" label="课程编号" width="200">
                </el-table-column>
                <el-table-column prop="courseCredit" label="课程学分" width="200">
                </el-table-column>
                <el-table-column prop="coursePreiod" label="课程学时" width="200">
                </el-table-column>
                <el-table-column prop="reviewType.typeName" label="考察类型" width="200">
                </el-table-column>
                <el-table-column prop="majorNum" label="专业" width="200">
                </el-table-column>
                
                <el-table-column prop="semester" label="开设学期" width="200">
                </el-table-column>
                <el-table-column label="操作" width="300">
                    <template slot-scope="scope">
                        <el-button type="text" @click="dialogFormVisible = true">修改</el-button>
                        <el-dialog title="修改" :visible.sync="dialogFormVisible">
                            <el-form :model="form">
                                <el-form-item label="课程编号" :label-width="formLabelWidth">
                                    <el-input v-model.number="form.courseNum" autocomplete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="课程名称" :label-width="formLabelWidth">
                                    <el-input v-model="form.courseName" autocomplete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="课程学分" :label-width="formLabelWidth">
                                    <el-input v-model.number="form.courseCredit" autocomplete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="课程学时" :label-width="formLabelWidth">
                                    <el-input v-model.number="form.coursePreiod" autocomplete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="考察类型" :label-width="formLabelWidth">
                                    <el-input v-model.number="form.reviewType" autocomplete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="专业" :label-width="formLabelWidth">
                                    <el-input v-model="form.majorNum" autocomplete="off"></el-input>
                                </el-form-item>
                                <el-form-item label="开设学期" :label-width="formLabelWidth">
                                    <el-input v-model="form.semester" autocomplete="off"></el-input>
                                </el-form-item>
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="reset">取 消</el-button>
                                <el-button type="primary" @click="formUpdate(scope.row.courseNum)">确 定</el-button>
                            </div>
                        </el-dialog>
                        <el-button @click="deleteById(scope.row.courseNum)" type="text" size="medium">删除</el-button>
                    </template>
                </el-table-column>


            </el-table>
            <!-- 对话框，移出 el-form-item -->
            <el-dialog title="添加" :visible.sync="dialogFormVisible2">
                <el-form :model="form2">
                    <el-form-item label="课程名称" :label-width="formLabelWidth">
                        <el-input v-model="form2.courseName" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="课程学分" :label-width="formLabelWidth">
                        <el-input v-model.number="form2.courseCredit" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="课程学时" :label-width="formLabelWidth">
                        <el-input v-model.number="form2.coursePreiod" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="考察类型" :label-width="formLabelWidth">
                    <el-select v-model="form2.reviewType.typeName" placeholder="请选择考察类型">
                        <el-option label="考试" value="考试"></el-option>
                        <el-option label="考核" value="考核"></el-option>
                    </el-select>
                </el-form-item>
                    <el-form-item label="专业" :label-width="formLabelWidth">
                        <el-input v-model="form2.majorNum" autocomplete="off"></el-input>
                    </el-form-item>

                    <el-form-item label="开设学期" :label-width="formLabelWidth">
                        <el-input v-model="form2.semester" autocomplete="off"></el-input>
                    </el-form-item>
                </el-form>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="reset">取 消</el-button>
                    <el-button type="primary" @click="insertExam">确 定</el-button>
                </div>
            </el-dialog>
        </el-main>
    </el-container>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            inputData: '',
            //绑定添加数据
            form2: {
                courseName: null,
                courseNum: null,
                courseCredit: null,
                coursePreiod: null,
                reviewType: {
                    typeName: '',
                    typeNum: ''
                },
                majorNum: null,
                semester: null,
            },
            //试卷详情
            // examData: {
            //     examNum: '',
            //     isReleased: '',
            //     teacher: [],
            //     course: [],
            //     modules: [],
            // },
            courseData: [],
            //绑定修改数据
            // form: {
            //     courseName: '',
            //     courseNum: '',
            //     courseCredit: '',
            //     coursePeriod: '',
            //     reviewType: '',
            //     majorNum: '',
            //     semester: '',
            // },
            form: {
                courseName: null,
                courseNum: null,
                courseCredit: null,
                coursePreiod: null,
                reviewType: null,
                majorNum: null,
                semester: null,
            },

            //试卷详情

            dialogTableVisible: false,
            dialogFormVisible: false,
            dialogFormVisible2: false,
            dialogVisible : false,
            formLabelWidth: '120px'
        }
    },
    methods: {
        handleClick(row) {
            console.log(row);
        },
        onSubmit() {
            console.log('submit!');
        },
        //查询全部
        getAll() {
            axios.get("/api/course").then((result) => {
                this.courseData = result.data.data;
            });
        },
        //模糊查询
        likeGet() {
            axios.get(`/api/course/${this.inputData}`).then((result) => {
                this.courseData = result.data.data;
            });

        },
        //修改表单
        formUpdate(id) {
            this.form.courseNum=id;
            axios.put("/api/course", this.form).then((result) => {
                if (result.data.code == 1) {
                    this.dialogFormVisible = false;
                    this.open2();
                    this.getAll();
                    this.reset();
                }
                else {
                    this.open3();

                }
            });

        },
        //删除数据
        deleteById(id) {
            axios.delete(`/api/course/${id}`).then((result) => {
                if (result.data.code == 1) {
                    this.open2();
                    this.getAll();
                }
                else {
                    this.open3();
                }


            });

        },
        //添加课程
        insertExam() {
            if(this.form2.reviewType.typeName=="考试"){
                this.form2.reviewType.typeNum=1;
            }else{
                this.form2.reviewType.typeNum=2;
            }
            axios.post("/api/course", this.form2).then((result) => {
                if (result.data.code == 1) {
                    this.getAll();
                    this.dialogFormVisible2=false;
                    this.open2();
                    this.reset();

                }
                else {
                    this.open3();
                }

            });

        },
        //重置
        reset() {
            this.form = '';
            this.dialogFormVisible=false;
            this.dialogFormVisible2=false;
        },
        //成功提示
        open2() {
            this.$message({
                message: '恭喜你，这是一条成功消息',
                type: 'success'
            });
        },
        //错误提示
        open3() {
            this.$message({
                message: '警告哦，这是一条警告消息',
                type: 'warning'
            });
        },

    },
    mounted() {
        this.getAll();

    },
}
</script>

<style scoped>
.el-container {
    min-height: 100vh; /* 至少占据整个视口高度 */
    max-height: 1200px; /* 最大高度限制 */
    overflow-y: auto; /* 如果内容超出高度，显示滚动条 */
    display: flex;
    flex-direction: column;
  }
  
  .el-header, .el-footer {
    flex: 0 0 auto; /* 固定高度 */
    background-color: #409EFF;
    color: azure;
    padding: 10px;
  }
  
  .el-main {
    flex: 1 1 auto; /* 自动占据剩余空间 */
    overflow-y: auto; /* 如果内容超出高度，显示滚动条 */
    padding: 20px;
    box-sizing: border-box;
  }
</style>

